import React, { Fragment, useState } from 'react'
import { Text } from '../Text'
import css from './Image.module.css'

export const Image = ({
  url = 'prueba3',
  alt = 'Imagen de prueba.',
  title = 'Imagen 1.',
  typeImg,
  dataStyle = '2',
  addClass
  //El dataStyle indica que diseño se requiere para el contenedor de la imagen: default,1,2,3,4.
}) => {
  //función que condiciona la img a enviar, de no mandar una url, toma por default otra img
  const [error, setError] = useState(false)

  const onError = (value) => {
    setError(value)
  }
  console.log(error)

  let imgToSee = !error
    ? `./images/${url}${typeImg}`
    : `data:image/png;base64,`

  return (
    <Fragment>
      <figure>
        <div className={css.containerImg} data-style={dataStyle}>
          <img
            src={imgToSee}
            onError={onError}
            alt={alt}
            data-style-img={dataStyle}
          />
        </div>
        <figcaption>
          <div className={css.containerText}>
            <Text
              type='p'
              children={
                <Fragment>
                  <b>{title}</b> <span>{alt}</span>
                </Fragment>
              }
            />
          </div>
        </figcaption>
      </figure>
    </Fragment>
  )
}

Image.defaultProps = {
  typeImg: '.png'
}
